"use client";

import { useEffect, useState } from 'react';
import { useAuth } from '@/context/AuthContext';
import { useRouter } from 'next/navigation';
import { db } from '@/utils/firebase';
import { collection, query, orderBy, getDocs } from 'firebase/firestore';
import Link from 'next/link';
import { ArrowLeft, FileText, Calendar, ArrowRight, Loader2, Trash2 } from 'lucide-react';

export default function Dashboard() {
    const { user, loading } = useAuth();
    const router = useRouter();
    const [reports, setReports] = useState<any[]>([]);
    const [fetching, setFetching] = useState(true);

    useEffect(() => {
        if (!loading && !user) {
            router.push('/login');
        }
    }, [user, loading, router]);

    useEffect(() => {
        const fetchReports = async () => {
            if (!user) return;

            try {
                const reportsRef = collection(db, `users/${user.uid}/reports`);
                const q = query(reportsRef, orderBy('createdAt', 'desc'));
                const querySnapshot = await getDocs(q);

                const fetchedReports = querySnapshot.docs.map(doc => ({
                    id: doc.id,
                    ...doc.data()
                }));

                setReports(fetchedReports);
            } catch (error) {
                console.error("Error fetching reports:", error);
            } finally {
                setFetching(false);
            }
        };

        if (user) {
            fetchReports();
        }
    }, [user]);

    if (loading || (fetching && user)) {
        return (
            <div className="min-h-screen flex items-center justify-center bg-slate-50">
                <Loader2 className="w-8 h-8 animate-spin text-indigo-600" />
            </div>
        );
    }

    if (!user) return null;

    return (
        <div className="min-h-screen bg-slate-50 py-12 font-sans">
            <div className="max-w-5xl mx-auto px-4">
                <div className="flex items-center justify-between mb-8">
                    <Link href="/" className="inline-flex items-center text-sm font-bold text-slate-400 hover:text-indigo-600 transition-colors group">
                        <ArrowLeft className="w-4 h-4 mr-1 group-hover:-translate-x-1 transition-transform" /> Back to Home
                    </Link>
                    <div className="flex items-center gap-3 bg-white px-4 py-2 rounded-2xl shadow-sm border border-slate-100">
                        <div className="w-10 h-10 rounded-full bg-indigo-100 flex items-center justify-center text-indigo-700 font-bold text-sm overflow-hidden">
                            {user.photoURL ? (
                                <img src={user.photoURL} alt={user.displayName || 'User'} className="w-full h-full object-cover" />
                            ) : (
                                user.email?.charAt(0).toUpperCase()
                            )}
                        </div>
                        <div className="flex flex-col">
                            <span className="text-sm font-bold text-slate-900">{user.displayName || 'User'}</span>
                            <span className="text-xs font-medium text-slate-500">{user.email}</span>
                        </div>
                    </div>
                </div>

                <div className="mb-10">
                    <h1 className="text-3xl font-black text-slate-900 mb-2">My Saved Reports</h1>
                    <p className="text-slate-500 font-medium">Access your previously calculated financial reports.</p>
                </div>

                {reports.length === 0 ? (
                    <div className="bg-white rounded-3xl p-12 text-center border border-slate-100 shadow-sm">
                        <div className="w-16 h-16 bg-slate-50 rounded-full flex items-center justify-center mx-auto mb-4">
                            <FileText className="w-8 h-8 text-slate-300" />
                        </div>
                        <h3 className="text-lg font-bold text-slate-900 mb-2">No reports saved yet</h3>
                        <p className="text-slate-500 mb-6 max-w-md mx-auto">
                            Go to any calculator, generate a result, and click "Save Report" to see it here.
                        </p>
                        <Link href="/" className="inline-flex items-center justify-center px-6 py-3 bg-indigo-600 text-white font-bold rounded-xl hover:bg-indigo-700 transition-all">
                            Explore Calculators
                        </Link>
                    </div>
                ) : (
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                        {reports.map((report) => (
                            <div key={report.id} className="bg-white rounded-2xl p-6 border border-slate-100 shadow-sm hover:shadow-md transition-all group">
                                <div className="flex justify-between items-start mb-4">
                                    <div className="inline-flex items-center gap-2 px-3 py-1 rounded-full bg-indigo-50 text-indigo-700 text-xs font-bold uppercase tracking-wide">
                                        {report.type}
                                    </div>
                                    <span className="text-xs font-medium text-slate-400 flex items-center gap-1">
                                        <Calendar className="w-3 h-3" />
                                        {report.createdAt?.toDate().toLocaleDateString()}
                                    </span>
                                </div>

                                <div className="mb-6">
                                    <div className="flex items-baseline gap-1">
                                        <span className="text-2xl font-black text-slate-900">
                                            {report.data?.breakdown?.total ?
                                                new Intl.NumberFormat('en-IN', { style: 'currency', currency: 'INR', maximumFractionDigits: 0 }).format(report.data.breakdown.total)
                                                : 'N/A'}
                                        </span>
                                        <span className="text-xs font-bold text-slate-500">Maturity Value</span>
                                    </div>
                                    <div className="mt-2 flex gap-4 text-sm text-slate-500">
                                        <div>
                                            <span className="block text-xs font-bold text-slate-400 uppercase">Investment</span>
                                            <span className="font-semibold text-slate-700">
                                                {report.data?.inputs?.investment ?
                                                    new Intl.NumberFormat('en-IN', { style: 'currency', currency: 'INR', maximumFractionDigits: 0 }).format(report.data.inputs.investment)
                                                    : 'N/A'}
                                            </span>
                                        </div>
                                        <div>
                                            <span className="block text-xs font-bold text-slate-400 uppercase">Tenure</span>
                                            <span className="font-semibold text-slate-700">{report.data?.inputs?.years} Years</span>
                                        </div>
                                    </div>
                                </div>

                                <div className="flex gap-3">
                                    {report.type === 'Fixed Deposit Calculator' && report.data?.inputs && (
                                        <Link
                                            href={`/fd-calculator?investment=${report.data.inputs.investment}&rate=${report.data.inputs.rate}&years=${report.data.inputs.years}`}
                                            className="flex-1 inline-flex items-center justify-center gap-2 px-4 py-2.5 bg-slate-900 text-white font-bold rounded-xl hover:bg-slate-800 transition-all text-sm"
                                        >
                                            Open Report <ArrowRight className="w-4 h-4" />
                                        </Link>
                                    )}
                                    {report.type === 'Lumpsum Calculator' && report.data?.inputs && (
                                        <Link
                                            href={`/lumpsum-calculator?investment=${report.data.inputs.investment}&rate=${report.data.inputs.rate}&years=${report.data.inputs.years}`}
                                            className="flex-1 inline-flex items-center justify-center gap-2 px-4 py-2.5 bg-slate-900 text-white font-bold rounded-xl hover:bg-slate-800 transition-all text-sm"
                                        >
                                            Open Report <ArrowRight className="w-4 h-4" />
                                        </Link>
                                    )}
                                    {report.type === 'SIP Calculator' && report.data?.inputs && (
                                        <Link
                                            href={`/sip-calculator?investment=${report.data.inputs.investment}&rate=${report.data.inputs.rate}&years=${report.data.inputs.years}`}
                                            className="flex-1 inline-flex items-center justify-center gap-2 px-4 py-2.5 bg-slate-900 text-white font-bold rounded-xl hover:bg-slate-800 transition-all text-sm"
                                        >
                                            Open Report <ArrowRight className="w-4 h-4" />
                                        </Link>
                                    )}
                                    {report.type === 'PPF Calculator' && report.data?.inputs && (
                                        <Link
                                            href={`/ppf-calculator?annualInvestment=${report.data.inputs.annualInvestment}&rate=${report.data.inputs.rate}&years=${report.data.inputs.years}`}
                                            className="flex-1 inline-flex items-center justify-center gap-2 px-4 py-2.5 bg-slate-900 text-white font-bold rounded-xl hover:bg-slate-800 transition-all text-sm"
                                        >
                                            Open Report <ArrowRight className="w-4 h-4" />
                                        </Link>
                                    )}
                                    {report.type === 'Income Tax Calculator' && report.data?.inputs && (
                                        <Link
                                            href={`/income-tax-calculator?grossIncome=${report.data.inputs.grossIncome}&deductions=${report.data.inputs.deductions}`}
                                            className="flex-1 inline-flex items-center justify-center gap-2 px-4 py-2.5 bg-slate-900 text-white font-bold rounded-xl hover:bg-slate-800 transition-all text-sm"
                                        >
                                            Open Report <ArrowRight className="w-4 h-4" />
                                        </Link>
                                    )}
                                    <button
                                        onClick={() => handleDeleteReport(report.id)}
                                        className="flex-1 inline-flex items-center justify-center gap-2 px-4 py-2.5 bg-red-600 text-white font-bold rounded-xl hover:bg-red-700 transition-all text-sm"
                                    >
                                        Delete Report <Trash2 className="w-4 h-4" />
                                    </button>
                                </div>
                            </div>
                        ))}
                    </div>
                )}
            </div>
        </div>
    );
}
